'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'

interface Option {
  id: number
  label: string
  color: string
  isCorrect: boolean
}

interface Question {
  prompt: string
  options: Option[]
}

const gameData = [
  {
    prompt: "Find the RED color",
    correctAnswer: "Red",
    options: [
      { id: 1, label: "Red", color: "bg-red-400", isCorrect: true },
      { id: 2, label: "Blue", color: "bg-blue-400", isCorrect: false },
      { id: 3, label: "Green", color: "bg-green-400", isCorrect: false },
      { id: 4, label: "Yellow", color: "bg-yellow-400", isCorrect: false }
    ]
  },
  {
    prompt: "Find the BLUE color",
    correctAnswer: "Blue",
    options: [
      { id: 1, label: "Purple", color: "bg-purple-400", isCorrect: false },
      { id: 2, label: "Blue", color: "bg-blue-400", isCorrect: true },
      { id: 3, label: "Orange", color: "bg-orange-400", isCorrect: false },
      { id: 4, label: "Pink", color: "bg-pink-400", isCorrect: false }
    ]
  },
  {
    prompt: "Find the GREEN color",
    correctAnswer: "Green",
    options: [
      { id: 1, label: "Yellow", color: "bg-yellow-400", isCorrect: false },
      { id: 2, label: "Red", color: "bg-red-400", isCorrect: false },
      { id: 3, label: "Green", color: "bg-green-400", isCorrect: true },
      { id: 4, label: "Blue", color: "bg-blue-400", isCorrect: false }
    ]
  },
  {
    prompt: "Find the YELLOW color",
    correctAnswer: "Yellow",
    options: [
      { id: 1, label: "Orange", color: "bg-orange-400", isCorrect: false },
      { id: 2, label: "Purple", color: "bg-purple-400", isCorrect: false },
      { id: 3, label: "Pink", color: "bg-pink-400", isCorrect: false },
      { id: 4, label: "Yellow", color: "bg-yellow-400", isCorrect: true }
    ]
  },
  {
    prompt: "Find the PURPLE color",
    correctAnswer: "Purple",
    options: [
      { id: 1, label: "Purple", color: "bg-purple-400", isCorrect: true },
      { id: 2, label: "Green", color: "bg-green-400", isCorrect: false },
      { id: 3, label: "Orange", color: "bg-orange-400", isCorrect: false },
      { id: 4, label: "Red", color: "bg-red-400", isCorrect: false }
    ]
  }
]

export default function Game() {
  const router = useRouter()
  const TOTAL_ROUNDS = 5
  const [round, setRound] = useState(1)
  const [score, setScore] = useState(0)
  const [question, setQuestion] = useState<Question | null>(null)
  const [feedback, setFeedback] = useState<string>('')
  const [showFeedback, setShowFeedback] = useState(false)
  const [gameComplete, setGameComplete] = useState(false)

  const generateQuestion = () => {
    try {
      if (round <= TOTAL_ROUNDS) {
        const questionData = gameData[round - 1]
        // Shuffle options for variety
        const shuffledOptions = [...questionData.options].sort(() => Math.random() - 0.5)
        setQuestion({
          prompt: questionData.prompt,
          options: shuffledOptions
        })
        setFeedback('')
        setShowFeedback(false)
      }
    } catch (error) {
      console.error("Error generating question:", error)
      setFeedback('Error loading question. Please try again.')
    }
  }

  useEffect(() => {
    generateQuestion()
  }, [round])

  const handleOptionSelect = (option: Option) => {
    try {
      if (!question || showFeedback) return

      if (option.isCorrect) {
        setFeedback('üéâ Correct! Great job!')
        setScore(score + 1)
        setShowFeedback(true)
        
        setTimeout(() => {
          if (round < TOTAL_ROUNDS) {
            setRound(round + 1)
          } else {
            setGameComplete(true)
          }
        }, 1500)
      } else {
        setFeedback('‚ùå Try again!')
        setShowFeedback(true)
        
        setTimeout(() => {
          setShowFeedback(false)
          setFeedback('')
        }, 1000)
      }
    } catch (error) {
      console.error("Error processing selection:", error)
      setFeedback('Something went wrong. Please try again.')
    }
  }

  const restartGame = () => {
    setRound(1)
    setScore(0)
    setGameComplete(false)
    setFeedback('')
    setShowFeedback(false)
    generateQuestion()
  }

  const goHome = () => {
    router.push('/')
  }

  if (!question && !gameComplete) {
    return (
      <div className="flex items-center justify-center">
        <div className="text-2xl font-bold text-gray-700">Loading game...</div>
      </div>
    )
  }

  if (gameComplete) {
    const percentage = Math.round((score / TOTAL_ROUNDS) * 100)
    let message = ""
    let emoji = ""
    
    if (percentage === 100) {
      message = "Perfect! You're amazing!"
      emoji = "üèÜ"
    } else if (percentage >= 80) {
      message = "Excellent work!"
      emoji = "‚≠ê"
    } else if (percentage >= 60) {
      message = "Good job!"
      emoji = "üëç"
    } else {
      message = "Keep practicing!"
      emoji = "üí™"
    }

    return (
      <div className="bg-white rounded-3xl p-8 shadow-2xl text-center max-w-md mx-auto">
        <div className="text-6xl mb-4">{emoji}</div>
        <h2 className="text-3xl font-bold text-gray-800 mb-4">Game Complete!</h2>
        <div className="bg-gradient-to-r from-blue-100 to-purple-100 rounded-2xl p-6 mb-6">
          <p className="text-2xl font-bold text-gray-700 mb-2">Your Score</p>
          <p className="text-4xl font-extrabold text-blue-600">{score} / {TOTAL_ROUNDS}</p>
          <p className="text-lg text-gray-600 mt-2">{percentage}% Correct</p>
        </div>
        <p className="text-xl font-semibold text-gray-700 mb-8">{message}</p>
        <div className="space-y-4">
          <button 
            onClick={restartGame}
            className="w-full py-3 px-6 bg-green-500 hover:bg-green-600 text-white text-xl font-bold rounded-2xl shadow-lg transform transition-all duration-200 hover:scale-105 active:scale-95"
          >
            üîÑ Play Again
          </button>
          <button 
            onClick={goHome}
            className="w-full py-3 px-6 bg-blue-500 hover:bg-blue-600 text-white text-xl font-bold rounded-2xl shadow-lg transform transition-all duration-200 hover:scale-105 active:scale-95"
          >
            üè† Home
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="w-full max-w-lg mx-auto">
      <div className="bg-white rounded-3xl p-6 shadow-2xl">
        {/* Header */}
        <div className="text-center mb-6">
          <div className="flex justify-between items-center mb-4">
            <div className="bg-blue-100 rounded-full px-4 py-2">
              <span className="text-blue-600 font-bold">Round {round}/{TOTAL_ROUNDS}</span>
            </div>
            <div className="bg-green-100 rounded-full px-4 py-2">
              <span className="text-green-600 font-bold">Score: {score}</span>
            </div>
          </div>
          <h2 className="text-2xl font-bold text-gray-800 mb-4">{question?.prompt}</h2>
          {feedback && (
            <div className={`text-xl font-bold p-3 rounded-2xl ${
              feedback.includes('Correct') 
                ? 'bg-green-100 text-green-700' 
                : 'bg-red-100 text-red-700'
            }`}>
              {feedback}
            </div>
          )}
        </div>

        {/* Options Grid */}
        <div className="grid grid-cols-2 gap-4">
          {question?.options.map(option => (
            <button
              key={option.id}
              onClick={() => handleOptionSelect(option)}
              disabled={showFeedback}
              className={`
                relative h-32 rounded-2xl shadow-lg transform transition-all duration-200 
                ${!showFeedback ? 'hover:scale-105 active:scale-95' : 'cursor-not-allowed opacity-75'}
                ${option.color} 
                flex flex-col items-center justify-center text-white font-bold text-lg
                border-4 border-white
              `}
            >
              <div className="text-3xl mb-2">
                {option.label === 'Red' && 'üî¥'}
                {option.label === 'Blue' && 'üîµ'}
                {option.label === 'Green' && 'üü¢'}
                {option.label === 'Yellow' && 'üü°'}
                {option.label === 'Purple' && 'üü£'}
                {option.label === 'Orange' && 'üü†'}
                {option.label === 'Pink' && 'ü©∑'}
              </div>
              <span className="text-shadow">{option.label}</span>
            </button>
          ))}
        </div>

        {/* Progress Bar */}
        <div className="mt-6">
          <div className="bg-gray-200 rounded-full h-3">
            <div 
              className="bg-gradient-to-r from-blue-400 to-purple-400 h-3 rounded-full transition-all duration-500"
              style={{ width: `${(round / TOTAL_ROUNDS) * 100}%` }}
            ></div>
          </div>
        </div>
      </div>
    </div>
  )
}
